.wrapper {
  padding: 20px; }
  .wrapper .input-wrap {
    position: relative;
    max-width: 320px; }
    .wrapper .input-wrap input {
      padding: 10px;
      min-width: 320px; }
    .wrapper .input-wrap img {
      position: absolute;
      top: 15px;
      right: 0px; }
  .wrapper label {
    font-weight: bold;
    margin-right: 10px; }
  .wrapper .list {
    margin-top: 20px; }
    .wrapper .list ul {
      list-style: none;
      display: inline-block;
      border: 1px solid;
      margin-top: -20px;
      min-width: 320px;
      padding: 0; }
      .wrapper .list ul li {
        background: #f9f9f9;
        padding: 6px 10px;
        border-bottom: 1px solid;
        cursor: pointer; }
        .wrapper .list ul li:hover {
          background: lightblue; }

.login-box {
  max-width: 500px;
  margin: 0 auto;
  padding-top: 50px;
  text-align: center; }
  .login-box button {
    margin-top: 20px; }
  .login-box h1 {
    margin-bottom: 20px; }
  .login-box input {
    margin-bottom: 20px; }
  .login-box .help-block {
    color: red;
    margin-top: -10px;
    text-align: left;
    margin-bottom: 10px; }
  .login-box img {
    margin-top: 15px;
    margin-left: 5px; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body .error {
    color: red; }

